@charset "UTF-8";
:root {
  --font-primary: proxima-nova, sans-serif;
  --font-display: alternate-gothic-extra-cond, sans-serif;
  --font-display-transform: none;
  --font-size-page-title: 2.5rem;
  --font-size-page-title-lg: 3.5rem;
  --line-height-page-title: 1.125;
  --gray-darker: #333;
  --gray-dark: #666;
  --gray: #999;
  --gray-light: #ccc;
  --gray-lighter: #eaeaea;
  --blue-heritage: #002d72;
  --blue-spirit: #67ace5;
  --orange: #cf4520;
  --maroon: #76232f;
  --purple: #a15a95;
  --green: #009b77;
  --blue: #0072ce;
  --yellow: #f1c400;
  --color-primary: var(--blue-heritage);
  --color-secondary: var(--blue-spirit);
}

html {
  font-family: var(--font-primary);
  line-height: 1.5;
}

body {
  background-color: #fff;
  font-size: 16px;
  font-size: 1em;
  font-weight: lighter;
  margin: 0;
  padding: 0;
  color: #31261d;
}

main {
  background-color: #eaeaea;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

*:focus {
  outline: 0;
  transition: 0.3s;
  box-shadow: 0 0 0 0.3rem rgba(0, 114, 206, 0.5);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  margin: 0 0 1rem;
  font-family: var(--font-primary);
}

h1 {
  text-transform: var(--font-display-transform);
  font-family: var(--font-display);
  font-size: var(--font-size-page-title);
  line-height: var(--line-height-page-title);
}
@media (min-width: 1024px) {
  h1 {
    font-size: var(--font-size-page-title-lg);
  }
}

h2 {
  text-transform: var(--font-display-transform);
  font-family: var(--font-display);
  font-size: var(--font-size-page-title);
  line-height: var(--line-height-page-title);
}
@media (min-width: 1024px) {
  h2 {
    font-size: var(--font-size-page-title-lg);
  }
}

h3 {
  font-size: 2rem;
  line-height: 1.125;
}

h4 {
  font-size: 1.75rem;
  line-height: 1.125;
}

h5 {
  font-size: 1.5rem;
  line-height: 1.125;
}

h6 {
  font-size: 1.25rem;
  line-height: 1.125;
}

a {
  color: var(--color-secondary);
  text-decoration: none;
  text-decoration-color: transparent;
  transition: all 0.3s;
}
a:hover {
  text-decoration: underline;
  text-decoration-color: inherit;
}
a.zn-arrow-link:after {
  content: "  \f105";
  display: inline;
  position: relative;
  left: 0;
  font-family: "Font Awesome 5 Free";
  font-weight: 700;
  font-style: normal;
  font-size: 1em;
  display: inline-block;
  line-height: 1;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-variant: normal;
  text-rendering: auto;
  transition: 0.3s;
}
a.zn-arrow-link:hover {
  text-decoration: none;
}
a.zn-arrow-link:hover:after {
  left: 0.125rem;
}

p {
  margin: 0 0 1rem;
  line-height: 1.5;
}
p a {
  text-decoration: underline;
}
p.intro {
  font-size: 1.25rem;
  color: #666;
  font-weight: 400;
}
p.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

button, .button {
  font-family: var(--font-primary);
  position: relative;
  font-size: 1rem;
  font-weight: 500;
  border: 0;
  border-radius: 0.25rem;
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: var(--color-secondary);
  margin: 0 0 1rem;
  line-height: 1.125;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
  -webkit-appearance: none;
}
button:after, .button:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: 0.25rem;
  background-color: #fff;
  opacity: 0;
  pointer-events: none;
  transition: ease-in-out 0.3s;
}
button:hover, .button:hover {
  text-decoration: none;
}
button:hover:after, .button:hover:after {
  opacity: 0.2;
}
button.disabled, button.inactive, .button.disabled, .button.inactive {
  cursor: not-allowed;
  background-color: #ccc;
}
button.disabled:hover, button.inactive:hover, .button.disabled:hover, .button.inactive:hover {
  background-color: #ccc;
}

.zn-button {
  font-family: var(--font-primary);
  position: relative;
  font-size: 1rem;
  font-weight: 500;
  border: 0;
  border-radius: 0.25rem;
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: var(--color-secondary);
  margin: 0 0 1rem;
  line-height: 1.125;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
  margin: 0;
}
.zn-button:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: 0.25rem;
  background-color: #fff;
  opacity: 0;
  pointer-events: none;
  transition: ease-in-out 0.3s;
}
.zn-button:hover {
  text-decoration: none;
}
.zn-button:hover:after {
  opacity: 0.2;
}

ul, ol {
  margin: 0 0 1rem;
  padding: 0 0 0 1.75rem;
  line-height: 1.125;
}
ul > li, ol > li {
  margin: 0;
}
ul > li + li, ol > li + li {
  margin-top: 0.25rem;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  margin: 1.25rem 0;
  border: 0;
  border-top: 1px solid #ccc;
  height: 0;
  width: auto;
  border-style: solid;
  clear: both;
}

table {
  margin-bottom: 2rem;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
table tr th {
  text-align: left;
  font-weight: 700;
  vertical-align: middle;
  padding: 0.75rem;
  line-height: 1.125;
  background-color: #666;
  color: #fff;
}
table tr td {
  vertical-align: middle;
  padding: 0.75rem;
  line-height: 1.125;
}
table tr:nth-child(even) {
  background-color: #eaeaea;
}

blockquote {
  padding: 0 0 0 1rem;
  border-left: 4px solid var(--color-secondary);
  color: #666;
  margin: 0 0 1rem;
  font-style: italic;
}

strong, b {
  font-weight: 700;
}

i, em {
  font-style: italic;
}

.site-width {
  max-width: 1440px;
  margin: auto;
  padding: 0 1.5rem;
}

.screen-reader {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.screen-reader:active, .screen-reader:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

dl {
  margin: 0 0 1rem;
}
dl .property {
  margin-bottom: 1rem;
}
dl dt {
  font-weight: 700;
}
dl dd {
  margin: 0;
  font-size: 1.25rem;
  font-weight: lighter;
}

.media-embeds {
  margin-bottom: 1.5rem;
}

body {
  overflow-x: hidden;
}
body:before {
  content: "";
  position: fixed;
  height: 100%;
  width: 100%;
  display: block;
  background: rgba(0, 0, 0, 0.65);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
  z-index: 9995;
}

@media all and (max-width: 1023px) {
  .zn-drawer {
    position: fixed;
    background-color: var(--color-primary);
    color: #fff;
    visibility: hidden;
    top: 0;
    right: 0;
    z-index: 9999;
    width: 90%;
    max-width: 350px;
    height: 100%;
    overflow: auto;
    box-sizing: border-box;
    transform: translate3d(100%, 0, 0);
    transition: all 0.5s;
  }
  .no-js .zn-drawer {
    display: block;
    position: relative;
    height: auto;
    width: auto;
    transform: none;
  }
}

.zn-site-shift {
  transition: transform 0.6s;
}

.zn-drawer-opened {
  overflow: hidden;
}
.zn-drawer-opened:before {
  visibility: visible;
  opacity: 1;
  transition: 0.7s;
}
.zn-drawer-opened .zn-drawer {
  visibility: visible;
  transform: translate3d(0, 0, 0);
}
.zn-drawer-opened .zn-site-shift {
  transform: translate3d(350px, 0, 0);
}

