form {
  margin-bottom: 1rem;
}

label, .label {
  display: inline-block;
  font-size: 1.25rem;
  font-weight: 700;
}

input[type=text],
input[type=password],
textarea,
select {
  font-size: 1rem;
  border-radius: 0.25rem;
  border: 1px solid #ccc;
  box-sizing: border-box;
  padding: 0.5rem;
  width: auto;
  transition: 0.3s;
}
input[type=text]:focus,
input[type=password]:focus,
textarea:focus,
select:focus {
  outline: 0;
  transition: 0.3s;
  box-shadow: 0 0 0 0.3rem rgba(0, 114, 206, 0.5);
}

textarea {
  min-height: 10rem;
}

input[type=submit] {
  font-family: var(--font-primary);
  position: relative;
  font-size: 1rem;
  font-weight: 500;
  border: 0;
  border-radius: 0.25rem;
  display: inline-block;
  padding: 0.5rem 1rem;
  background-color: var(--color-secondary);
  margin: 0 0 1rem;
  line-height: 1.125;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
  -webkit-appearance: none;
}
input[type=submit]:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: 0.25rem;
  background-color: #fff;
  opacity: 0;
  pointer-events: none;
  transition: ease-in-out 0.3s;
}
input[type=submit]:hover {
  text-decoration: none;
}
input[type=submit]:hover:after {
  opacity: 0.2;
}

input [type=checkbox], input [type=radio] {
  padding: 0;
  margin: 0.25rem;
}

fieldset {
  border: 0;
  padding: 0;
  margin: 0;
}
